<template>
  <div id="wrap">
    
  <gnb @close="close()" v-if="show == 0" :show="show"/>
  <ct  v-if="show == 0" :아이네="아이네" :징버거="징버거" :릴파="릴파" :주르르="주르르" :고세구="고세구" :비챤="비챤"/>
  <play v-if="show == 0"/>

</div>

<router-view @open="open()" :show="show"></router-view>
</template>

<script>
import gnb from './components/gnb.vue';
import ct from './components/content.vue';
import play from './components/play.vue';

import 아이네 from './components/아이네';
import 징버거 from './components/징버거';
import 릴파 from './components/릴파';
import 주르르 from './components/주르르';
import 고세구 from './components/고세구';
import 비챤 from './components/비챤';

export default {
  name: 'App',
  data(){
    return{
      아이네:아이네,
      징버거:징버거,
      릴파:릴파,
      주르르:주르르,
      고세구:고세구,
      비챤:비챤,
      show:0,
    }
  },
  methods:{
    close(){
      this.show = 1
    },
    open(){
      this.show = 0
    }
  },
  components: {
    gnb,
    ct,
    play,
  
  }
}
</script>

