<template>
  <div class="loginpage" v-if="show==1">
    <img src="../assets/메인로고.png">
    <div class="login-wrapper">
      <h2>Login</h2>
        <form @submit.prevent="fnLogin" method="post" action="서버의url" id="login-form">
            <input  v-model="user_id" type="text" name="userName" placeholder="지원자 이름 입력">
            <input   v-model="user_pw" type="password" name="userPassword" placeholder="휴대폰 뒷자리 입력">
            <label for="remember-check">
                <input type="checkbox" id="remember-check">아이디 저장하기
            </label>
            <input @click="$emit('open')" type="submit" value="Login">
        </form>
    </div>
  </div>
</template>

<script>
export default {
  name:'NAme',
  data(){
    return{
      user_id: '정우혁',
      user_pw: '0603',
      
    }
  },
  props:{
    show:Boolean,
  },
  methods: {
    fnLogin() {
      if (this.user_id == '') {
        alert('ID를 입력하세요.')
        return
      }

      if (this.user_pw == '') {
        alert('비밀번호를 입력하세요.')
        return
      }

      else{
        alert('로그인 되었습니다.')
      }
    }
  }
 

}
</script>

<style>
@import './style.css';
</style>